body {
    padding : 0;
    margin:  0;
}

.container {
    width : 1120px;
    margin : auto;

    .errmsg {
        width : 800px;
        margin : 100px auto;
        border : 1px solid #ccc;
        padding : 20px 30px;

        h3 {
            padding : 0;
            margin : 0;
            font-size : 20px;
            color : #900;
            border-bottom: 1px solid #500;
            padding-bottom : 10px;
            margin-bottom : 15px;
        }
        p {
            font-size :14px;
            font-style: italic;
            color : #555;
            padding : 0;
            margin : 0;
        }
    }

    .table {
        margin-top: 50px;
        margin-bottom: 50px;
        width : 100%;
        border-top: 1px solid #444444;
        border-collapse: collapse;

        th {
            background-color : #eee;
        }
        th,
        td {
            border-bottom: 1px solid #444444;
            padding : 10px;
        }

        .textCenter {
            text-align: center;
        }
        .textRight {
            text-align: right;
        }
    }
}